.PopperArrow {
  position: absolute;
}

.PopperArrow__in {
  content: '';
  display: block;

  /* .  1  PopperArrow.tsx.    `.PopperArrow[data-placement^='bottom']`. */
  transform: translateY(1px);
}

.PopperArrow[data-placement^='top'] .PopperArrow__in {
  transform: rotate(180deg) translateY(1px); /* .  1  PopperArrow.tsx */
}

.PopperArrow[data-placement^='left'] .PopperArrow__in {
  transform: rotate(90deg) translateY(1px); /* .  1  PopperArrow.tsx */
}

.PopperArrow[data-placement^='right'] .PopperArrow__in {
  transform: rotate(270deg) translateY(1px); /* .  1  PopperArrow.tsx */
}
